.loading {
    color: var(--color-loading);
}

.todo {
    padding: var(--padding-vspace) var(--padding-hspace);
    margin: 0 0 var(--padding-vspace) 0;
    background-color: var(--color-silver);
}

.check {
    margin: 0 var(--padding-hspace) 0 0;
    vertical-align: text-bottom;
}

.action {
    color: var(--color-loading);
    cursor: pointer;
}

.delete {
    composes: action;
    margin: 0 0 0 var(--padding-hspace);
}
